.popup__container {
    position: relative;
    max-width: 430px;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    box-sizing: border-box;
    padding: 34px 36px 36.5px;
}

@media screen and (max-width: 530px) {
    .popup__container {
        max-width: 425px;
        width: 75%;
        padding: 30px 20px;
}
    }